//importando
@import "colors";

$font: 'monospace';

//variaveis de cores declaradas no arquivo _cores.scss
div{
    background-color: $dark-gray;
    p{
        color: $red;        
    }
}

//aninhando varias tags
ul{
    font: $font;
    li{
        list-style: none;
        color: $red;
        
        a{
            color: $blue;
            text-decoration: none;
        }
    }
    
}

